.mySQLManagement {
  height: 100%;
}
.mySQLManagement .breadcrumbLink-left .tabs-item {
  cursor: pointer;
  float: left;
  margin-left: 26px;
}
.mySQLManagement .breadcrumbLink-left .blue-bottom {
  position: relative;
}
.mySQLManagement .breadcrumbLink-left .blue-bottom:after {
  content: '';
  display: inline-block;
  width: 75px;
  height: 2px;
  bottom: -3px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  position: absolute;
  background: #4B85FF;
}
.mySQLManagement .hostTable {
  height: 100%;
  box-sizing: border-box;
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
}
.mySQLManagement .hostTable .btn {
  height: 50px;
}
.mySQLManagement .hostTable .btn .ant-btn {
  margin-right: 10px;
}
.mySQLManagement .hostTable .table-list {
  flex: 1 1;
  overflow: auto;
}
.mySQLManagement .hostTable .operation {
  margin-right: 10px;
}
.mySQLManagement .hostTable .operation:last-child {
  margin-right: 0;
}
