.menuManage {
  height: 100%;
}
.menuManage .p-content-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
}
.menuManage .p-content-wrapper .btn {
  height: 50px;
}
.menuManage .p-content-wrapper .table-list {
  flex: 1 1;
  overflow: auto;
}
.menuManage .p-content-wrapper .table-list .operation {
  margin-right: 10px;
}
.menuManage .p-content-wrapper .table-list .operation:last-child {
  margin-right: 0;
}
.menuManage .p-content-wrapper .table-list .ant-table-row-level-1 {
  background: #F4F8FB !important;
}
.setMenuModal .avatar-uploader > .ant-upload {
  width: 80px;
  height: 80px;
}
.setMenuModal .priceWrap {
  width: 62px;
  height: 62px;
  position: relative;
}
.setMenuModal .priceWrap .close {
  font-size: 16px;
  color: #bfbfbf;
  position: absolute;
  top: -7px;
  right: -7px;
  z-index: 222;
}
