.addPaper .p-content-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 70px;
}
.addPaper .p-content-wrapper .conster-wrap {
  width: 960px;
  min-width: 960px;
}
.addPaper .p-content-wrapper .conster-wrap .title {
  font-size: 20px;
  font-weight: 500;
  color: #535B6C;
  line-height: 28px;
  text-align: center;
}
.addPaper .p-content-wrapper .conster-wrap .lineHeight {
  line-height: 60px;
  margin-bottom: 0;
}
.addPaper .p-content-wrapper .conster-wrap .table-title {
  height: 30px;
  line-height: 30px;
  display: flex;
  justify-content: space-between;
}
.addPaper .p-content-wrapper .conster-wrap .table-title span {
  margin-right: 30px;
}
.addPaper .p-content-wrapper .conster-wrap .table-title .testType {
  font-size: 16px;
  font-weight: 500;
}
.addPaper .p-content-wrapper .conster-wrap .table-title .right-btn span {
  color: #4897FF;
  cursor: pointer;
  margin-right: 15px;
}
.addPaper .p-content-wrapper .conster-wrap .tableClass {
  margin: 10px 0 30px 0;
}
.addPaper .p-content-wrapper .table-wrap-blue {
  width: 100%;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 14px;
  color: #4897FF;
  cursor: pointer;
  border: 1px dashed #4897FF;
}
.addPaper .p-content-wrapper .dashed {
  width: 100%;
  border-bottom: 1px dashed #F4F7FD;
}
.addPaper .bottom-btn {
  width: 100%;
  padding: 15px 0;
  text-align: center;
  position: absolute;
  left: 0px;
  bottom: 0px;
  background: #fff;
  box-shadow: 0 -2px 3px -1px #D0D0D0;
}
.addPaperModal .search-wrap-two span {
  color: rgba(0, 0, 0, 0.85);
}
