.databaseResources {
  height: 100%;
  background-color: #F4F9FF;
}
.databaseResources .left-wrap {
  width: 300px;
  margin-right: 20px;
  float: left;
}
.databaseResources .right-wrap {
  float: left;
}
.databaseResources .breadcrumbLink-left .left-wrap {
  width: 320px;
}
.databaseResources .p-content-wrapper {
  height: 100%;
  padding: 0;
}
.databaseResources .p-content-wrapper .left-wrap {
  height: 100%;
  min-width: 300px;
  overflow-y: auto;
  background-color: #FFFFFF;
  padding: 0 20px;
}
.databaseResources .p-content-wrapper .right-wrap {
  height: 100%;
  width: calc(100% - 320px);
  min-width: calc(100% - 320px);
  padding: 20px;
  box-sizing: border-box;
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
}
.databaseResources .p-content-wrapper .right-wrap .btn {
  height: 50px;
}
.databaseResources .p-content-wrapper .right-wrap .btn .ant-btn {
  margin-right: 10px;
}
.databaseResources .p-content-wrapper .right-wrap .table-list {
  flex: 1 1;
  overflow: auto;
}
.databaseResources .ant-tree {
  overflow-y: auto;
}
.databaseResources .ant-tree li .ant-tree-node-content-wrapper {
  width: 100%;
  height: 33px;
  line-height: 33px;
}
.databaseResources .ant-tree-treenode-switcher-open {
  padding: 0;
}
.databaseResources .ant-tree li .ant-tree-node-content-wrapper.ant-tree-node-selected {
  background: #E8EEF9;
}
.databaseResources .ant-tree li span.ant-tree-iconEle {
  margin-top: 4px;
}
.databaseResources .ant-tree li .ant-tree-node-content-wrapper {
  width: 215px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
