.roleManagement {
  height: 100%;
}
.roleManagement .p-content-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.roleManagement .p-content-wrapper .btn {
  height: 50px;
}
.roleManagement .p-content-wrapper .table-list {
  flex: 1 1;
  overflow: auto;
}
.roleManagement .p-content-wrapper .table-list .operation {
  margin-right: 10px;
}
.roleManagement .p-content-wrapper .table-list .operation:last-child {
  margin-right: 0;
}
.roleManagement .p-content-wrapper .table-list .ant-table-row-level-1 {
  background: #F4F8FB !important;
}
.setRoleModal .ant-form-item {
  margin-bottom: 5px;
}
.Authorization .ant-modal-body {
  max-height: 450px;
  overflow: auto;
}
