.branchManagement {
  height: 100%;
}
.branchManagement .p-content-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.branchManagement .p-content-wrapper .btn {
  height: 50px;
}
.branchManagement .p-content-wrapper .table-list {
  flex: 1 1;
  overflow: auto;
}
.branchManagement .p-content-wrapper .table-list .operation {
  margin-right: 10px;
}
.branchManagement .p-content-wrapper .table-list .operation:last-child {
  margin-right: 0;
}
.branchManagement .p-content-wrapper .table-list .ant-table-row-level-1 {
  background: #F4F8FB !important;
}
.branchManagement .p-content-wrapper .table-list .ant-table-row-level-2 {
  background: #EEF3F8 !important;
}
.branchManagement .p-content-wrapper .table-list .ant-table-expanded-row .ant-table-body {
  border: none !important;
}
.setDeptModal .ant-form-item {
  margin-bottom: 5px;
}
