.jobAuthorization .ant-alert-warning .ant-alert-icon {
  font-size: 18px;
}
.jobAuthorization .body-wrap {
  height: 400px;
  overflow: hidden;
}
.jobAuthorization .body-wrap .top-header {
  height: 40px;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
}
.jobAuthorization .body-wrap .list-wrap {
  height: calc(100% - 40px);
  overflow-y: auto;
}
.jobAuthorization .body-wrap .list-wrap .item {
  float: left;
  background: url(data:image/png;base64,) no-repeat;
  background-size: 160px 40px;
  width: 160px;
  height: 40px;
  line-height: 40px;
  margin-right: 20px;
  margin-bottom: 18px;
  cursor: pointer;
  overflow: hidden;
  box-sizing: border-box;
  padding: 0 25px 0 10px;
}
.jobAuthorization .body-wrap .list-wrap .item:nth-child(3n) {
  margin-right: 0;
}
.jobAuthorization .body-wrap .list-wrap .checked {
  background: url(data:image/png;base64,) no-repeat;
  background-size: 160px 40px;
}
