.p-bread-wrapper {
  position: relative;
  left: 0px;
  top: -35px;
  float: right;
  width: 100%;
}
.p-bread-wrapper .breadcrumbLink-left {
  float: left;
  color: #535B6C;
  font-size: 18px;
}
.details-name {
  width: 100%;
  height: 19px;
  font-size: 14px;
  font-family: MicrosoftYaHei;
  color: #333333;
  line-height: 19px;
  margin-top: 20px;
}
.details-node {
  float: left;
  margin-left: 20px;
  width: 80px;
}
.dataDetails-name {
  width: 80px;
  float: left;
}
.dataDetails {
  float: right;
}
.p-content-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  overflow: auto;
  padding: 20px;
}
.p-content-wrapper > div {
  min-width: 1150px;
}
.p-content-wrapper .ant-table-body {
  border: 1px solid #e8e8e8;
  border-bottom: none;
}
.p-content-wrapper .ant-table-placeholder {
  border-left: 1px solid #e8e8e8;
  border-right: 1px solid #e8e8e8;
}
.p-content-wrapper .ant-table-body-inner .ant-table-fixed {
  border: 1px solid #e8e8e8;
  border-left: none;
}
a {
  text-decoration: none;
}
a:hover,
a:active,
a:focus {
  text-decoration: none;
}
/*清浮动*/
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after,
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.ant-tabs-nav div.ant-tabs-tab-active {
  font-weight: bold;
}
.ant-transfer-list-content-item {
  white-space: inherit !important;
  margin-top: 10px;
  text-overflow: inherit !important;
}
.ant-transfer-list-content > .LazyLoad {
  height: auto !important;
}
