body {
  background: #f8f8f8;
}
.p-main {
  height: 100vh;
  display: flex;
  display: -webkit-flex;
  align-items: center;
  justify-content: center;
  background: #f8f8f8;
}
.p-main .login-form {
  margin-top: 40px;
}
.p-main .itemStr {
  line-height: 0px;
  margin-left: 5px;
  color: red;
  font-size: 14px;
}
.p-main .p-landing {
  width: 1200px;
  height: 500px;
  background: #FFFFFF;
  border-radius: 15px;
}
.p-main .p-landing .p-login {
  float: left;
  width: 50%;
  background: #4897FF;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  height: 100%;
}
.p-main .p-landing .p-login .p-loginPicture {
  width: 70%;
  height: 45px;
  position: relative;
  top: 50px;
  left: 100px;
}
.p-main .p-landing .p-login h1 {
  color: #FFFFFF;
  text-align: center;
  position: relative;
  top: 230px;
}
.p-main .p-landing .p-login h3 {
  color: #FFFFFF;
  text-align: center;
  position: relative;
  top: 230px;
}
.p-main .p-landing .p-ashore {
  width: 50%;
  float: left;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  height: 100%;
}
.p-main .p-landing .p-ashore .disembark {
  width: 500px;
  height: 1000px;
  padding: 50px;
}
