.testStatistics {
  height: 100%;
}
.testStatistics .p-content-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
}
.testStatistics .p-content-wrapper .addBtn {
  margin-bottom: 10px;
  text-align: right;
}
.testStatistics .p-content-wrapper .addBtn .ant-btn {
  margin-right: 10px;
}
.testStatistics .p-content-wrapper .table-list {
  flex: 1 1;
  overflow: auto;
}
.testStatistics .p-content-wrapper .table-list .operation {
  margin-right: 10px;
}
.testStatistics .p-content-wrapper .table-list .operation:last-child {
  margin-right: 0;
}
.testStatistics .p-content-wrapper .pagination {
  text-align: right;
  margin-top: 10px;
}
