.loginLog {
  height: 100%;
}
.loginLog .p-content-wrapper {
  width: 100%;
  overflow-x: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
}
.loginLog .p-content-wrapper .addBtn {
  margin-bottom: 20px;
}
.loginLog .p-content-wrapper .table-list {
  flex: 1 1;
  overflow: auto;
}
.loginLog .p-content-wrapper .table-list .operation {
  margin-right: 10px;
}
.loginLog .p-content-wrapper .table-list .operation:last-child {
  margin-right: 0;
}
.loginLog .p-content-wrapper .ant-calendar-picker {
  width: 100% !important;
}
