.userManage {
  height: 100%;
}
.userManage .p-content-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.userManage .p-content-wrapper .addBtn {
  margin-bottom: 20px;
}
.userManage .p-content-wrapper .table-list {
  flex: 1 1;
  overflow: auto;
}
.userManage .p-content-wrapper .table-list .operation {
  margin-right: 10px;
}
.userManage .p-content-wrapper .table-list .operation:last-child {
  margin-right: 0;
}
.setUserModal .setUser-box {
  height: 450px;
  overflow-y: auto;
}
