body {
  background-color: #F4F9FF;
}
.layout-box {
  height: 100%;
}
.layout-box .content-side .ant-menu-submenu-open .ant-menu-submenu-title {
  color: #4B85FF;
}
.ant-menu-vertical .ant-menu-item::after,
.ant-menu-vertical-left .ant-menu-item::after,
.ant-menu-vertical-right .ant-menu-item::after,
.ant-menu-inline .ant-menu-item::after {
  border-right: 0px !important;
}
.ant-menu-inline,
.ant-menu-vertical,
.ant-menu-vertical-left {
  border-right: 0px !important;
}
.ant-menu-item > a {
  padding-left: 50px;
}
.ant-menu-sub.ant-menu-inline > .ant-menu-item {
  padding-left: 0px  !important;
  padding-right: 0;
}
.ant-menu-sub.ant-menu-inline > .ant-menu-item .active {
  background: #d9d9d9 !important;
  color: #535B6C !important;
}
.ant-menu-sub.ant-menu-inline > .ant-menu-item .ant-menu-submenu-title {
  color: #4B85FF;
}
.calendar {
  float: right;
  margin-top: 21px;
  margin-right: 140px;
  color: #FFFFFF;
}
.layout {
  position: absolute;
  left: 200px;
  top: 0px;
  right: 0px;
  height: 100vh;
}
.layout .layout-header {
  position: absolute;
  left: 0px;
  top: 0;
  height: 60px;
  background: #333;
  right: 0;
  left: -200px;
}
.layout .layout-header h2 {
  font-size: 16px;
  font-weight: bold;
  padding-left: 30px;
  line-height: 60px;
}
.layout .layout-header .layout-user {
  position: absolute;
  right: 20px;
  top: 15px;
  border-radius: 50%;
  cursor: pointer;
}
.layout .layout-header .layout-user img {
  display: inline-block;
  border-radius: 50%;
  width: 30px;
  height: 30px;
}
.layout .layout-header .layout-log {
  float: left;
  height: 60px;
}
.content-box {
  position: absolute;
  top: 110px;
  left: 0px;
  right: 0px;
  bottom: 0;
}
.content-box .content-box-main {
  height: 100%;
}
.content-box .content-box-main .content-side {
  position: absolute;
  width: 160px;
  left: 20px;
  top: 0;
  background: #fff;
  bottom: 20px;
}
.content-box .content-box-main .content-main,
.content-box .content-box-main .content-main-bench {
  position: absolute;
  right: 20px;
  left: 200px;
  top: 0;
  background: #fff;
  bottom: 20px;
}
.content-box .content-box-main .content-main-bench {
  left: 20px;
}
.content-box .content-side .active {
  position: relative;
  font-weight: bold;
}
.content-box .content-side .active::before {
  content: '';
  position: absolute;
  left: -24px;
  width: 3px;
  height: 16px;
  top: 50%;
  margin-top: -8px;
  background: #7C8495;
}
.content-side ul {
  padding: 20px 0 0 24px;
}
.content-side ul li {
  height: 16px;
  line-height: 16px;
  list-style: none;
  margin-bottom: 20px;
}
.content-side ul li a {
  color: #7C8495;
  text-decoration: none;
}
.layout-menu-icon {
  margin-left: -8px;
}
.layout-menu-icon .iconfont {
  font-size: 26px;
}
