.dictionaryManagement {
  height: 100%;
}
.dictionaryManagement .p-content-wrapper {
  height: 100%;
}
.dictionaryManagement .p-content-wrapper .add-btn {
  color: #4B85FF;
  cursor: pointer;
}
.dictionaryManagement .p-content-wrapper .add-btn span {
  margin-left: 5px;
}
.dictionaryManagement .p-content-wrapper .table-wrap {
  flex: 1 1;
  overflow: auto;
}
.dictionaryManagement .p-content-wrapper .left-table {
  width: 450px;
  min-width: 450px;
  padding-right: 20px;
  float: left;
}
.dictionaryManagement .p-content-wrapper .left-table .blueText {
  color: #4B85FF;
  background: #F4F9FF;
}
.dictionaryManagement .p-content-wrapper .left-table .iconList .anticon {
  font-size: 16px;
  color: #4B85FF;
  cursor: pointer;
  margin-right: 7px;
}
.dictionaryManagement .p-content-wrapper .right-table .iconList .anticon {
  font-size: 16px;
  color: #4B85FF;
  cursor: pointer;
  margin-right: 10px;
}
