.testManagement {
  height: 100%;
}
.testManagement .p-content-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
}
.testManagement .p-content-wrapper .btn-search {
  height: 50px;
  text-align: right;
}
.testManagement .p-content-wrapper .btn-search span {
  width: 100px;
}
.testManagement .p-content-wrapper .btn-search input {
  width: 200px;
}
.testManagement .p-content-wrapper .table-list {
  flex: 1 1;
  overflow: auto;
}
.testManagement .p-content-wrapper .table-list .operation {
  margin-right: 10px;
}
.testManagement .p-content-wrapper .table-list .operation:last-child {
  margin-right: 0;
}
