.testArrange {
  height: 100%;
}
.testArrange .p-content-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
}
.testArrange .p-content-wrapper .btn-search {
  height: 50px;
  display: flex;
  justify-content: space-between;
}
.testArrange .p-content-wrapper .btn-search .search {
  display: flex;
}
.testArrange .p-content-wrapper .btn-search .search span {
  width: 100px;
  line-height: 32px;
}
.testArrange .p-content-wrapper .table-list {
  flex: 1 1;
  overflow: auto;
}
.testArrange .p-content-wrapper .table-list .operation {
  margin-right: 10px;
}
.testArrange .p-content-wrapper .table-list .operation:last-child {
  margin-right: 0;
}
